@import url(https://fonts.googleapis.com/css2?family=Muli&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Muli', sans-serif;
}

#header{
  width: 100%;
  height: 80vh;
  /* margin-top: 70px; */
}

#header h1{
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color: black;
}

#header .brand-name{
  color:#3498db;
}

#header h2{
  color: #484848;
  font-size: 24px;
}

#header .btn-get-started{
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: #3498db;
  border: 2px solid #3498db;
  text-decoration: none;
}

#header .btn-get-started:hover{
  background: #3498db;
  color: white;
}

.header-img{
  text-align: right;
}

.header-img:hover{
  opacity: 0.8;
}

#header .animated{
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@keyframes up-down{
  0%{
    transform: translateY(10px);
  }
  
  100%{
    transform: translateY(-10px);
  }
}

/** Footer */


#footer{
  height: 100px;
  width: 100%;
  text-align: center;
}

#footer a{
  color: black;
  text-decoration: none;
  font-size: 19px;
  margin: 10px;
  font-weight: bold;
}

#footer a:hover{
  color: rgba(23, 76, 145, 0.562);
  text-decoration: underline !important;
}

#footer img:hover{
  opacity: 0.6;
}


.reveals-main {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.reveals-main > div {
  font-weight: 800;
  font-size: 48px;
  will-change: transform, opacity;
  overflow: hidden;
}

